import { createSlice } from "@reduxjs/toolkit";
import { eng, ita } from "THE_BLOG/THE_BLOG_Settings";

export type ILang = {
  siteLanguage: string
}

const initialState: ILang = {
  siteLanguage: ita,
};

const lang = createSlice({
  name: 'lang',
  initialState: initialState,
  reducers: {
    TOGGLE_SITE_LANGUAGE: (state) => {
      state.siteLanguage = state.siteLanguage === ita ? eng : ita
    },
  }
})

export default lang